<template>
  <v-card min-height="80vh" max-height="80vh" style="overflow-y:auto" class="mx-auto">
    <v-col class="mx-auto" cols="12">
      <grid-products @reloadData="getSearchResults()" :productsArray="resultsSearchBusiness" :titleCard="`Resultados de tu búsqueda ${this.$route.query.input_search}`"></grid-products>
    </v-col>
  </v-card>
</template>
<script>
import GridProducts from "@/components/Products/GridProducts";
import { mapState, mapActions } from "vuex";
export default {
  components: {
    gridProducts: GridProducts
  },
  computed: {
    ...mapState("products/stores", ["resultsSearchBusiness"])
  },
  data() {
    return {};
  },
  mounted() {
    this.getSearchResults();
  },
  methods: {
    ...mapActions("products/stores", ["getProductsSearchByBusiness"]),
    getSearchResults() {
      if (this.$route.params.id && this.$route.query.input_search) {
        const data = {
          id_store: this.$route.params.id,
          search: this.$route.query.input_search
        };
        this.getProductsSearchByBusiness(data);
      }
    }
  },
  watch: {
    $route(to, from) {
      this.getSearchResults();
    }
  }
};
</script>

